<style scoped>
.title {
  font-size: 16px;
  margin-bottom: 10px;
  color: #478fca !important;
}
.title i {
  margin-right: 5px;
}
</style>

<template>
  <div id="stationselector">
    <div class="title">
      <i class="fa fa-subway"></i>
      选择车站
    </div>
    <el-select v-model="value" placeholder="请选择" style="width:220px">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  name: "stationselector",
  data() {
    return {
      options: null,
      value: ""
    };
  },
  methods: {
    handleOption(stationData) {
      var option = [];
      for (var i = 0; i < stationData.length; i++) {
        option[i] = {};
        option[i].value = stationData[i].stationID;
        option[i].label = stationData[i].stationName;
      }

      this.options = option;
      console.log(this.options)
    }
  },
  created() {
    let stationData = this.$store.getters.stationList;
    console.log(stationData)
    this.handleOption(stationData);
  }
};
</script>