<style scoped>
.title {
  font-size: 16px;
  margin-bottom: 10px;
  color: #478fca !important;
}
.title i {
  margin-right: 5px;
}
</style>

<template>
  <div id="categoryselectormul">
    <div class="title">
      <i class="fa fa-list"></i>
      能耗类型
    </div>
    <el-select v-model="value" multiple placeholder="请选择" style="width:220px">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  name: "categoryselectormul",
  data() {
    return {
      options: null,
      value: ""
    };
  },
  methods: {
    handleOption(categoryData) {
      var option = [];
      for (var i = 0; i < categoryData.length; i++) {
        option[i] = {};
        option[i].value = categoryData[i].CategoryID;
        option[i].label = categoryData[i].CategoryName;
      }

      this.options = option;
      console.log(this.options);
    }
  },
  created() {
    let categoryData = this.$store.getters.categoryList;
    console.log(categoryData);
    this.handleOption(categoryData);
  }
};
</script>