<style scoped>
h2 {
  text-align: center;
  padding: 0px;
  font-size: 18px;
}
#chart {
  width: 100%;
  height: 700px;
  /* border: 1px solid red; */
  margin: 0 auto;
}
.stationsContainer {
  padding: 5px 0px 15px 45px;
}
.buttonContainer {
  width: 100%;
  height: 100%;
  margin-top:20px 
}
.title {
  font-size: 25px;
  padding: 10px 10px 0px 20px;
  color: #478fca !important;
}
</style>

<template>
  <div id="occcomparetime">
    <el-row>
      <el-row :gutter="6">
        <div class="title">
          分时分析
          <i class="fa fa-star"></i>
          <el-divider style="margin:12px 0 !important;"></el-divider>
        </div>
      </el-row>
      <el-row :gutter="18">
        <el-col :span="5">
          <div class="stationsContainer">
            <StationsSelector></StationsSelector>
          </div>
          <div class="stationsContainer">
            <CategorySelectorMul></CategorySelectorMul>
          </div>
          <div class="stationsContainer">
            <DateFromTo></DateFromTo>
          </div>
          <div class="buttonContainer">
            <SubmitButton></SubmitButton>
          </div>
        </el-col>
        <el-col :span="19">
          <h2>分时分析</h2>
          <div ref="chart" id="chart"></div>
        </el-col>
      </el-row>
    </el-row>
  </div>
</template>

<script>
import echarts from "echarts";
import StationsSelector from "../components/StationsSelector";
import CategorySelectorMul from "../components/CategorySelectorMul";
import DateFromTo from "../components/DateFromTo";
import SubmitButton from "../components/SubmitButton";

export default {
  name: "occcomparetime",
  components: {
    StationsSelector,
    CategorySelectorMul,
    DateFromTo,
    SubmitButton
  },
  data() {
    return {};
  },
  mounted() {
    this.getEchartData();
  },
  methods: {
    getEchartData() {
      let myChart = echarts.init(this.$refs.chart);
      let option = {
        color: ["#f44"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        xAxis: [
          {
            type: "category",
            data: [
              "1月",
              "2月",
              "3月",
              "4月",
              "5月",
              "6月",
              "7月",
              "8月",
              "9月",
              "10月",
              "11月",
              "12月"
            ],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: "value"
          }
        ],
        series: [
          {
            name: "每月花费",
            type: "bar",
            barWidth: "60%",
            data: [995, 666, 444, 858, 654, 236, 645, 546, 846, 225, 547, 356]
          }
        ]
      };
      myChart.setOption(option);
      //建议加上以下这一行代码，不加的效果图如下（当浏览器窗口缩小的时候）。超过了div的界限（红色边框）
      window.addEventListener("resize", function() {
        myChart.resize();
      });
    }
  }
};
</script>


