<style scoped>
.title {
  font-size: 16px;
  margin-bottom: 10px;
  color: #478fca !important;
}
.title i {
  margin-right: 5px;
}
.datePicker{
    margin-top:10px;
}

.datePicker p{
  color: #478fca !important;
  margin-bottom: 5px; 
}
</style>

<template>
  <div id="datefromto">
    <div class="title">
      <i class="fa fa-calendar"></i>
      查询时间
    </div>
    <el-radio-group v-model="radio">
      <el-radio :label="3">年</el-radio>
      <el-radio :label="6">月</el-radio>
      <el-radio :label="9">日</el-radio>
    </el-radio-group>
    <template>
      <div class="datePicker" v-if="radio === 3">
        <p class="demonstration">选择年</p>
        <el-date-picker v-model="value" type="year" placeholder="选择年"></el-date-picker>
      </div>
      <div class="datePicker" v-else-if="radio === 6">
        <p class="demonstration">选择月</p>
        <el-date-picker v-model="value" type="month" placeholder="选择月"></el-date-picker>
      </div>
      <div class="datePicker" v-else>
        <p class="demonstration">选择日期</p>
        <el-date-picker v-model="value" type="date" placeholder="选择日期"></el-date-picker>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: "datefromto",
  data() {
    return {
      value: "",
      radio: 3,
      value: "",
      datePicker:
        '<span class="demonstration">默认</span><el-date-picker v-model="value" type="date" placeholder="选择日期"></el-date-picker>',
      monthPicker:
        '<span class="demonstration">默认</span><el-date-picker v-model="value" type="month" placeholder="选择月"></el-date-picker>',
      yearPicker:
        '<span class="demonstration">默认</span><el-date-picker v-model="value" type="year" placeholder="选择年"></el-date-picker>'
    };
  }
};
</script>